CrB,UAAW,CACX,kBAAmB,CACnB,UAAW,CACX,SAAU,CACV,gBAAiB,CACjB,kBAAmB,CA/C9B,yBAoDW,oBAAqB,CACrB,UAAW,CACX,kBAAmB,CACnB,UAAW,CACX,SAAU,CACV,gBAAiB,CACjB,kBAAmB,CACxB,OC1ET,YAAa,CACb,kBAAmB,CACnB,sBAAuB,CACvB,WAAY,CACZ,iBACI,WAAY,CCGpB,MACI,2BAAmB,CACnB,qBAAa,CACb,wBAAgB,CACnB,aAGG,2BAAmB,CACnB,qBAAa,CACb,wBAAgB,CACnB,cAGG,2BAAmB,CACnB,qBAAa,CACb,wBAAgB,CAHpB,+BAOY,kBAAmB,CAP/B,sBAWS,4BAA4B,CAChC,EAiBD,qBAAsB,CACtB,QAAS,CACT,SAAU,CACb,KAGG,eAAgB,CAChB,0BAA2B,CAC9B,KAIG,6CAA8C,CAC9C,kCAAmC,CACnC,0BAA2B,CAC3B,oCAAqC,CACrC,gBAAiB,CAEpB,MAIG,eAAgB,CACnB,eAOG,mBAAoB,CACvB,WAGG,gBAAiB,CACjB,aAAc,CACd,iBAAkB,CAClB,kBAAmB,CACtB,YAGG,UAAW,CACX,YAAa,CAChB,OAEG,YAAa,CACb,sBAAuB,CACvB,kBAAmB,CACtB,eAGG,YAAa,CACb,iBAAkB,CAClB,YAAa,CACb,qBAAsB,CACtB,YAAa,CAChB,aAGG,UAAW,CACX,MAAO,CACV,iDAxDD,KAgEQ,sBAAuB,CAC1B,CAGL,0CApEA,KAsEQ,sBAAuB,CAC1B,CAIL,qCA3EA,KA6EQ,aAAc,CACd,sBAAuB,CAC1B","sourcesContent":[".btn-switch {\r\n    position: fixed;\r\n    bottom: 2rem;\r\n    left: 2rem;\r\n}\r\n\r\n.checkbox {\r\n    opacity: 0;\r\n    position: absolute;\r\n}\r\n\r\n.label {\r\n    background-color: #282c35;\r\n    border-radius: 50px;\r\n    cursor: pointer;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: space-between;\r\n    padding: 5px;\r\n    position: relative;\r\n    height: 26px;\r\n    width: 50px;\r\n\r\n}\r\n\r\n.label .ball {\r\n    background-color: #fff;\r\n    border-radius: 50%;\r\n    position: absolute;\r\n    top: 2px;\r\n    left: 2px;\r\n    height: 22px;\r\n    width: 22px;\r\n    transform: translateX(0px);\r\n    transition: transform 0.2s linear;\r\n}\r\n\r\n.checkbox:checked+.label .ball {\r\n    transform: translateX(24px);\r\n}\r\n\r\n\r\n.fa-moon {\r\n    color: #f1c40f;\r\n}\r\n\r\n.fa-sun {\r\n    color: #f39c12;\r\n}","/* ===============================================================================\r\n   TABLE OF CONTENT\r\n   ===============================================================================\r\n   1. General\r\n   2. Make Container\r\n   3. Make Rows\r\n   4. Make Columns\r\n   5. Include Mixin\r\n*/\r\n\r\n/* =============================================================================== \r\n   1. General\r\n   =============================================================================== */\r\n\r\n/* Reset CSS */\r\n*,\r\n*::before,\r\n*::after {\r\n    box-sizing: border-box;\r\n    margin: 0;\r\n    padding: 0;\r\n}\r\n\r\n/* Variables */\r\n$grid-gutter-width: 30px !default;\r\n$grid-columns: 12 !default;\r\n\r\n$container-max-widths: (\r\n    sm: 540px,\r\n    md: 720px,\r\n    lg: 960px,\r\n    xl: 1140px,\r\n) !default;\r\n\r\n/* Set Breakpoints Value */\r\n$grid-breakpoints: (\r\n    xs: 0,\r\n    sm: 576px,\r\n    md: 768px,\r\n    lg: 992px,\r\n    xl: 1200px,\r\n) !default;\r\n\r\n/* Function Media Query */\r\n@function breakpoint-infix($name, $breakpoints: $grid-breakpoints) {\r\n    @return if(breakpoint-min($name, $breakpoints)==null, \"\", \"-#{$name}\");\r\n}\r\n\r\n@function breakpoint-min($name, $breakpoints: $grid-breakpoints) {\r\n    $min: map-get($breakpoints, $name);\r\n    @return if($min != 0, $min, null);\r\n}\r\n\r\n@mixin media-breakpoint-up($name, $breakpoints: $grid-breakpoints) {\r\n    $min: breakpoint-min($name, $breakpoints);\r\n    @if $min {\r\n        @media (min-width: $min) {\r\n            @content;\r\n        }\r\n    }\r\n    @else {\r\n        @content;\r\n    }\r\n}\r\n\r\n/* =============================================================================== \r\n   2. Make Container\r\n   =============================================================================== */\r\n\r\n@mixin make-container($gutter: $grid-gutter-width) {\r\n    width: 100%;\r\n    padding-right: $gutter / 2;\r\n    padding-left: $gutter / 2;\r\n    margin-right: auto;\r\n    margin-left: auto;\r\n}\r\n\r\n@mixin make-container-max-widths($max-widths: $container-max-widths, $breakpoints: $grid-breakpoints) {\r\n\r\n    @each $breakpoint,\r\n    $container-max-width in $max-widths {\r\n        @include media-breakpoint-up($breakpoint, $breakpoints) {\r\n            max-width: $container-max-width;\r\n        }\r\n    }\r\n}\r\n\r\n/* =============================================================================== \r\n   3. Make Rows\r\n   =============================================================================== */\r\n\r\n@mixin make-row($gutter: $grid-gutter-width) {\r\n    display: flex;\r\n    flex-wrap: wrap;\r\n    margin-right: -$gutter / 2;\r\n    margin-left: -$gutter / 2;\r\n}\r\n\r\n/* =============================================================================== \r\n   4. Make Columns\r\n   =============================================================================== */\r\n\r\n@mixin make-col-ready($gutter: $grid-gutter-width) {\r\n    position: relative;\r\n    width: 100%;\r\n    padding-right: $gutter / 2;\r\n    padding-left: $gutter / 2;\r\n}\r\n\r\n@mixin make-col($size, $columns: $grid-columns) {\r\n    flex: 0 0 percentage($size / $columns);\r\n    max-width: percentage($size / $columns);\r\n}\r\n\r\n@mixin make-col-offset($size, $columns: $grid-columns) {\r\n    $num: $size / $columns;\r\n    margin-left: if($num==0, 0, percentage($num));\r\n}\r\n\r\n@mixin make-grid-columns($columns: $grid-columns, $gutter: $grid-gutter-width, $breakpoints: $grid-breakpoints) {\r\n    %grid-column {\r\n        position: relative;\r\n        width: 100%;\r\n        padding-right: $gutter / 2;\r\n        padding-left: $gutter / 2;\r\n    }\r\n\r\n    @each $breakpoint in map-keys($breakpoints) {\r\n        $infix: breakpoint-infix($breakpoint, $breakpoints);\r\n\r\n        @for $i from 1 through $columns {\r\n            .col#{$infix}-#{$i} {\r\n                @extend %grid-column;\r\n            }\r\n        }\r\n\r\n        .col#{$infix},\r\n        .col#{$infix}-auto {\r\n            @extend %grid-column;\r\n        }\r\n\r\n        @include media-breakpoint-up($breakpoint, $breakpoints) {\r\n            .col#{$in